
.fixed-header .table {
	width: auto;
}
.fixed-header thead {
	display: block;
	position: relative;
}
.fixed-header tbody {
	display: block;
	height: 200px;
	overflow-y: scroll;
}
.fixed-column {
	overflow-x: scroll;
	margin-left: 100px;
}
.fixed-column th, .fixed-column td {
	white-space: nowrap;
}
.fixed-column tr th:first-child, .fixed-column tr td:first-child {
	position: absolute;
	width: 100px;
	top: auto;
	margin-left: -100px;
	margin-top: -1px;
}
